html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1220px;
  padding: 0px 10px;
  margin: 0px auto;
}

body {
  color: #1b1818;
  font-size: 16px;
  line-height: 19px;
  font-weight: 400;
  font-family: "Roboto Condensed", sans-serif;
}

.application-btn {
  display: inline-block;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  color: #fefefe;
  background: #c5ac2d;
  padding: 16px 19px 15px;
  border: 1px solid transparent;
  transition: all 0.5s;
}
.application-btn:hover {
  background: #fefefe;
  border: 1px solid #c5ac2d;
  color: #c5ac2d;
}

.header {
  background-color: #332208;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}
.header .menu__list {
  display: flex;
}
.header .menu__item {
  margin: 0 15px;
}
.header .menu__list-link {
  text-transform: uppercase;
  color: #fefefe;
  font-size: 18px;
  line-height: 21px;
  padding-bottom: 5px;
  transition: all 0.5s;
  border-bottom: 1px solid transparent;
}
.header .menu__list-link:hover {
  border-bottom: 1px solid #fefefe;
}
.header .menu__btn {
  display: none;
}

.phone {
  color: #fefefe;
  font-size: 18px;
  line-height: 21px;
}

.main {
  padding-bottom: 100px;
}
.main__content {
  text-align: center;
  padding-top: 90px;
  width: 50%;
}
.main__heading {
  display: flex;
  padding: 50px 0 30px;
}
.main__title {
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 48px;
  line-height: 58px;
  margin: 0 auto 20px;
  max-width: 455px;
}
.main__text {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  margin: 0 auto 60px;
  max-width: 470px;
}
.main .slider {
  width: 50%;
}
.main .slider .slick-dots {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.main .slider__text {
  padding-right: 100px;
  text-align: right;
  font-family: "Fira Sans", sans-serif;
  font-style: normal;
  font-weight: 500;
}
.main .slider__text span + span {
  padding-left: 12px;
}
.main .slick-dots {
  list-style: none;
  text-align: center;
}
.main .slick-dots button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background-color: #332208;
  border: none;
  border-radius: 50%;
  padding: 0;
}
.main .slick-active button {
  width: 16px;
  height: 16px;
}
.main__bottom {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.main__items {
  width: 50%;
  display: flex;
  justify-content: space-evenly;
}
.main__item {
  border: 1px solid #1b1818;
  padding: 10px;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: all 0.5s;
}
.main__item:hover {
  transform: scale(1.25);
}
.main__inks {
  padding-right: 80px;
}
.main__link {
  color: #1b1818;
  border-bottom: 1px solid #1b1818;
}
.main__link + .main__link {
  margin-left: 20px;
}

.rent {
  padding: 100px 0;
}
.rent__inner {
  display: flex;
  justify-content: space-between;
}
.rent__content {
  max-width: 300px;
  margin-bottom: 20px;
}
.rent__title {
  font-family: "Fira Sans", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 30px;
}
.rent__text {
  margin-bottom: 20px;
}
.rent__text + .rent__text {
  margin-bottom: 40px;
}
.rent__item {
  border: 1px solid #332208;
  width: 250px;
  min-height: 350px;
  padding: 0 35px 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.rent__item-numb {
  font-size: 96px;
  line-height: 115px;
  font-family: "Fira Sans";
  font-weight: 500;
  opacity: 0.05;
}
.rent__item-title {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
}

.benefits {
  padding: 100px 0px;
}
.benefits__title {
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 50px;
}
.benefits__items {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.benefits__list {
  width: 300px;
  list-style: none;
}
.benefits__list-item {
  margin-bottom: 27px;
  padding-right: 26px;
  position: relative;
  text-align: right;
}
.benefits__list-item::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #332208;
  right: 0;
  top: 3px;
}
.benefits__list-item:nth-child(1), .benefits__list-item:nth-child(7) {
  margin-right: 10px;
}
.benefits__list-item:nth-child(2), .benefits__list-item:nth-child(6) {
  margin-right: 60px;
}
.benefits__list-item:nth-child(3), .benefits__list-item:nth-child(5) {
  margin-right: 110px;
}
.benefits__list-item:nth-child(4) {
  margin-right: 155px;
}
.benefits__content {
  width: 300px;
  text-align: right;
}
.benefits__text {
  margin-bottom: 30px;
  text-align: right;
}
.benefits__text + .benefits__text {
  width: 267px;
  margin-left: auto;
}

.video-popup {
  background-color: #c5ac2d;
  text-transform: uppercase;
  color: #fefefe;
  padding: 6px 34px 6px 64px;
  text-align: center;
  display: inline-block;
  position: relative;
}
.video-popup::before {
  content: "";
  width: 25px;
  height: 25px;
  position: absolute;
  background-image: url("../images/video-btn.svg");
  top: 12px;
  left: 34px;
}

.reviews {
  padding: 100px 0;
  background-color: #332208;
  color: #fefefe;
}
.reviews__title {
  padding-bottom: 70px;
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}
.reviews__item {
  border: 1px solid #c5ac2d;
  padding: 45px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 270px;
  min-height: 300px;
  text-align: center;
  margin-right: 30px;
}
.reviews__item-name {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}
.reviews__slider .slick-dots {
  list-style: none;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;
}
.reviews__slider .slick-dots button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background-color: #c5ac2d;
  border: none;
  border-radius: 50%;
  padding: 0;
  margin: 0 5px;
  line-height: 12px;
}
.reviews__slider .slick-active button {
  width: 16px;
  height: 16px;
}
.reviews .slick-initialized .slick-active {
  display: flex;
}

.feedback {
  padding: 100px 0;
}
.feedback__inner {
  display: flex;
  align-items: center;
}
.feedback__images {
  width: 50%;
}
.feedback__form {
  width: 415px;
  margin: 0px auto;
}
.feedback__title {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #1b1818;
  margin-bottom: 5px;
}
.feedback__text {
  margin-bottom: 30px;
}
.feedback__input {
  display: block;
  padding: 20px 0;
  width: 100%;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.feedback__input::placeholder {
  color: black;
  font-style: 16px;
  line-height: 19px;
}
.feedback__input + .feedback__input {
  margin-bottom: 50px;
}
.feedback__btn {
  margin-top: 50px;
}

.footer {
  padding: 100px 0 24px;
  background-color: #332208;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 21px;
}
.footer__social {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer__social-link {
  margin: 0 15px;
}
.footer .menu__list {
  display: flex;
}
.footer .menu__item {
  margin: 0 15px;
}
.footer .menu__list-link {
  text-transform: uppercase;
  color: #fefefe;
  font-size: 18px;
  line-height: 21px;
  padding-bottom: 5px;
  transition: all 0.5s;
  border-bottom: 1px solid transparent;
}
.footer .menu__list-link:hover {
  border-bottom: 1px solid #fefefe;
}
.footer .menu__btn {
  display: none;
}

@media (max-width: 1250px) {
  .benefits__images {
    width: 40%;
  }
}
@media (max-width: 1140px) {
  .rent__content {
    max-width: none;
    width: 100%;
  }
  .rent__inner {
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
@media (max-width: 1000px) {
  .main__heading {
    display: block;
  }
  .main__content {
    width: 100%;
    padding-top: 30px;
  }
  .main .slider {
    width: 100%;
  }
  .main__bottom {
    display: block;
  }
  .main__items {
    width: 100%;
    flex-wrap: wrap;
  }
  .main__links {
    padding-right: 0;
    padding-top: 30px;
    text-align: center;
  }

  .benefits__items {
    flex-wrap: wrap;
  }
  .benefits__content {
    width: 100%;
    margin-top: 30px;
  }
  .benefits__text + .benefits__text {
    width: 100%;
  }

  .footer .menu {
    display: none;
  }
}
@media (max-width: 860px) {
  .header .menu {
    order: 1;
  }
  .header .menu__list {
    display: block;
    position: absolute;
    top: 100px;
    left: 0;
    width: 100%;
    background-color: #332208;
    text-align: center;
    padding: 20px 0;
    transform: translateY(-500px);
    z-index: 999;
    transition: all 0.5s;
  }
  .header .menu__list--active {
    transform: translateY(0);
  }
  .header .menu__list-link {
    padding: 20px 0;
    display: block;
  }
  .header .menu__btn {
    display: block;
    width: 35px;
    height: 24px;
    border: none;
    cursor: pointer;
    border-top: 2px solid #fefefe;
    border-bottom: 2px solid #fefefe;
    background-color: transparent;
    position: relative;
  }
  .header .menu__btn::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fefefe;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  .rent__content {
    text-align: center;
  }
  .rent__item {
    margin: 30px;
    width: 100%;
    min-height: 275px;
  }
  .rent__item-title {
    margin-bottom: 10px;
  }

  .feedback__images {
    display: none;
  }

  .footer,
.feedback,
.reviews,
.benefits,
.rent {
    padding: 50px 0;
  }
}
@media (max-width: 525px) {
  .main .slider__text {
    padding-right: 0;
    text-align: right;
  }
  .main .slider__text span {
    display: block;
  }
  .main .slider__images {
    width: 300px;
  }
  .main__item {
    margin-top: 20px;
    width: 100%;
    min-height: 120px;
  }
  .main__links {
    text-align: center;
  }
  .main__link {
    margin: 10px 15px !important;
    display: inline-block;
  }

  .rent__item {
    margin: 30px 0px;
  }

  .benefits__list {
    width: 100%;
  }
  .benefits__list-item {
    margin-right: 0 !important;
    text-align: left;
    padding-right: 0;
    padding-left: 26px;
  }
  .benefits__list-item::after {
    right: auto;
    left: 0;
  }
  .benefits__images {
    width: 70%;
  }

  .footer__top {
    flex-direction: column;
    align-items: center;
  }
  .footer .phone {
    margin-top: 30px;
    display: inline-block;
  }
}